.post__textarea {
    width: 600px;
    height: 200px;
    margin-top: 40px;
    padding: 10px;
}

.post__button {
    display: block;
    margin-top: 10px;
    width: 250px;
    height: 50px;
    background: transparent;
    border: 1px solid #13CE66;
    border-radius: 50px;
    position: relative;
    z-index: 2;
}

.post__button::before {
    content: '';
    display: block;
    background-color: #13ce672f;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50px;
}