.profile__img-box {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    border: 4px solid #13CE66;
    position: relative;
    outline: none;
    z-index: 2;
}

.profile__img {
    z-index: 1;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 125px;
    height: 125px;
    border-radius: 50%;
}

.profile__text {
    font-size: 40px;
}

