.textarea {
    width: 600px;
    height: 200px;
    resize: none;
    outline: none;
    padding: 10px;
    margin-top: 40px;
}

.textarea__error {
    border-color: red;
}

.error__text {
    color: black;
    background-color: red;
    min-width: 100px;
    height: 35px;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    padding-left: 15px;
    left: 620px;
    top: 55%;
    transform: translateY(-50%);
}

.block {
    position: relative;
}